import PropTypes from 'prop-types';
import * as React from 'react';
import {
  Button,
  Card,
  CardHeader,
  Checkbox,
  ListItemText,
  ListItem,
  ListItemIcon,
  List,
  Grid,
  Box,
  TextField,
  Stack,
  Divider,
  useMediaQuery
} from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { Icon } from '@iconify/react';
import arrowIosForwardFill from '@iconify/icons-eva/arrow-ios-forward-fill';
import arrowIosUpwardFill from '@iconify/icons-eva/arrow-ios-upward-fill';
import arrowIosDownwardFill from '@iconify/icons-eva/arrow-ios-downward-fill';
import arrowHeadRightFill from '@iconify/icons-eva/arrowhead-right-fill';
import arrowHeadUpFill from '@iconify/icons-eva/arrowhead-up-fill';
import arrowHeadDownFill from '@iconify/icons-eva/arrowhead-down-fill';
import arrowHeadLeftFill from '@iconify/icons-eva/arrowhead-left-fill';
import arrowIosBackFill from '@iconify/icons-eva/arrow-ios-back-fill';

function not(a, b) {
  return a?.filter((value) => !b.map((item) => item.codename).includes(value));
}
function not2(a, b) {
  return a?.filter((value) => !b?.map((item) => item.codename).includes(value.codename));
}

function union(a, b) {
  const bWithoutInfo = b?.map((codename) => codename.codename);
  return [...a, ...not(bWithoutInfo, a)];
}

function notwithinfo(a, b, arr) {
  return a
    ?.filter((value) => !b.map((item) => item.codename).includes(value))
    .map((codename) => {
      const permission = arr?.find((item) => item.codename === codename);
      return {
        name: permission.name,
        codename
      };
    });
}

function intersection(a, b) {
  return a?.filter((value) => b?.map((item) => item.codename).includes(value));
}

TransferList.propTypes = {
  leftArr: PropTypes.array,
  right: PropTypes.array,
  setLeft: PropTypes.func,
  setRight: PropTypes.func
};

export default function TransferList({ leftArr, right, setLeft, setRight }) {
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('sm'));
  const [checked, setChecked] = React.useState([]);
  const [searchText, setSearchText] = React.useState('');

  const left = not2(leftArr, right) || [];

  const leftChecked = intersection(checked, left);
  const rightChecked = intersection(checked, right);

  const leftCheckedWithInfo = leftChecked?.map((codename) => {
    const permission = left?.find((item) => item.codename === codename);
    return {
      name: permission.name,
      codename
    };
  });
  const leftWithoutInfo = left?.map((codename) => codename.codename);
  const rightCheckedWithInfo = rightChecked?.map((codename) => {
    const permission = right?.find((item) => item.codename === codename);
    return {
      name: permission.name,
      codename
    };
  });
  const rightWithoutInfo = right?.map((codename) => codename.codename);

  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  const numberOfChecked = (items) => intersection(checked, items).length;

  const handleToggleAll = (items) => () => {
    if (numberOfChecked(items) === items.length) {
      setChecked(not(checked, items));
    } else {
      setChecked(union(checked, items));
    }
  };

  const handleAllRight = () => {
    setRight(right.concat(left));
    setLeft([]);
  };

  const handleCheckedRight = () => {
    setRight([...leftCheckedWithInfo, ...right]);
    setLeft(notwithinfo(leftWithoutInfo, leftCheckedWithInfo, left));
    setChecked(not(checked, leftCheckedWithInfo));
  };

  const handleCheckedLeft = () => {
    setLeft([...rightCheckedWithInfo, ...left]);
    setRight(notwithinfo(rightWithoutInfo, rightCheckedWithInfo, right));
    setChecked(not(checked, rightCheckedWithInfo));
  };

  const handleAllLeft = () => {
    setLeft(left.concat(right));
    setRight([]);
  };

  const filterPermissions = (permissions, searchText) =>
    permissions?.filter(
      (permission) =>
        (permission.name && permission.name.toLowerCase().includes(searchText.toLowerCase())) ||
        (permission.codename && permission.codename.toLowerCase().includes(searchText.toLowerCase()))
    );

  const customList = (title, items) => (
    <Card sx={{ width: isDesktop ? 400 : 280, height: 400, display: 'flex', flexDirection: 'column' }}>
      <CardHeader
        sx={{ px: 2, py: 1 }}
        avatar={
          <Checkbox
            onClick={handleToggleAll(items)}
            checked={numberOfChecked(items) === items.length && items.length !== 0}
            indeterminate={numberOfChecked(items) !== items.length && numberOfChecked(items) !== 0}
            disabled={items.length === 0}
            inputProps={{
              'aria-label': 'Tous les éléments sélectionnés'
            }}
          />
        }
        title={title}
        subheader={`${numberOfChecked(items)}/${items.length} sélectionné${numberOfChecked(items) > 1 ? 's' : ''}`}
      />
      <Divider />
      <Stack sx={{ px: 2, pt: 2 }}>
        <TextField
          label="Rechercher"
          fullWidth
          variant="outlined"
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
        />
      </Stack>
      <Box sx={{ flexGrow: 1, overflow: 'auto' }}>
        <List dense component="div" role="list">
          {filterPermissions(items, searchText)?.map((value) => {
            const labelId = `transfer-list-item-${value.codename}-label`;

            return (
              <ListItem key={value.codename} role="listitem" button onClick={handleToggle(value.codename)}>
                <ListItemIcon>
                  <Checkbox
                    checked={checked.indexOf(value.codename) !== -1}
                    tabIndex={-1}
                    disableRipple
                    inputProps={{
                      'aria-labelledby': labelId
                    }}
                  />
                </ListItemIcon>
                <ListItemText id={labelId} primary={value.name} />
              </ListItem>
            );
          })}
        </List>
      </Box>
    </Card>
  );

  return (
    <Grid container spacing={2} justifyContent="center" alignItems="center">
      <Grid item>{customList('Liste Autorisations', left)}</Grid>
      <Grid item>
        <Grid container direction="column" alignItems="center">
          <Button
            sx={{ my: 0.5 }}
            variant="outlined"
            size="small"
            onClick={handleAllRight}
            disabled={left?.length === 0}
            aria-label="move all right"
          >
            <Icon icon={isDesktop ? arrowHeadRightFill : arrowHeadDownFill} width={22} height={22} />
          </Button>
          <Button
            sx={{ my: 0.5 }}
            variant="outlined"
            size="small"
            onClick={handleCheckedRight}
            disabled={leftChecked?.length === 0}
            aria-label="move selected right"
          >
            <Icon icon={isDesktop ? arrowIosForwardFill : arrowIosDownwardFill} width={22} height={22} />
          </Button>
          <Button
            sx={{ my: 0.5 }}
            variant="outlined"
            size="small"
            onClick={handleCheckedLeft}
            disabled={rightChecked?.length === 0}
            aria-label="move selected left"
          >
            <Icon icon={isDesktop ? arrowIosBackFill : arrowIosUpwardFill} width={22} height={22} />
          </Button>
          <Button
            sx={{ my: 0.5 }}
            variant="outlined"
            size="small"
            onClick={handleAllLeft}
            disabled={right?.length === 0}
            aria-label="move all left"
          >
            <Icon icon={isDesktop ? arrowHeadLeftFill : arrowHeadUpFill} width={22} height={22} />
          </Button>
        </Grid>
      </Grid>
      <Grid item>{customList('Listes Choix', right)}</Grid>
    </Grid>
  );
}
